.btn
  &__primary
    @apply bg-gold text-white border border-gold uppercase text-center text-sm leading-8 tracking-[-0.1px] block px-6 py-1 w-full lg:max-w-56 whitespace-nowrap hover:bg-[#464646] hover:border-[#464646]
    @extend .hs-transition--default
  &__secondary
    @apply bg-white text-gold border border-gold uppercase text-center text-sm leading-8 tracking-[-0.1px] block px-6 py-1 w-full lg:max-w-56 whitespace-nowrap hover:bg-gold hover:text-white
    @extend .hs-transition--default